import React, { useContext } from 'react'
import { useFormik, yupToFormErrors } from "formik";
import * as Yup from "yup";
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { CartContext } from '../context/CartContext';
export default function Login() {
  let {setuserToken}=useContext(CartContext);
  let navegate= useNavigate();
  let validationSchema = Yup.object({
    email:Yup.string().required(),
    password:Yup.string().required()
  })
  let formik= useFormik({
    initialValues:{
      email:"",
      password:""
    },
    validationSchema,
    onSubmit:(values)=>{
      // console.log(values);
      postData(values)
    }
  })

  async function postData(objval){
      let {data}= await axios.post('https://alrayademo-back.appssquare.com/api/admin/login',objval);
      console.log(data);

      if(data.message == "success"){
        localStorage.setItem("token",data.token);
        setuserToken(true);
        navegate('/home');
      }
  }
  return (
    <div className='container'>
      <div className='login'>
      <div className='m-auto shadow-lg col-md-5 p-2  '>
            <h2 className='text-center'>Login</h2>
            <hr />
          <form onSubmit={formik.handleSubmit}>
            <div className='my-3'>
              {/* <i className='fa-regular fa-user'></i> */}
              
              <input type="text" placeholder='Email' onChange={formik.handleChange}  id='email' className='form-control ' />
              <p className="text-danger fs-6 p-1">{formik.errors.email}</p>
            </div>
          
            <div className='my-3'>
            {/* <i className="fa-regular fa-key"></i> */}

              <input type="password" onChange={formik.handleChange} placeholder='Passowrd' id='password' className='form-control' />
              <p className="text-danger fs-6 p-1">{formik.errors.password}</p>
            </div>
            <button type="submit" className="btn ">
                Login
              </button>
          </form>
      </div>
      </div>
      

    </div>
  )
}
