import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useRef } from 'react';

export default function Area() {
    const token=localStorage.getItem("token");
    let [userData,setuserData]=useState([]);
    let unique=0;
    let index=0;
    let nameInput= useRef();
    let depInput= useRef();
    useEffect(()=>{
        getData();
    },[])
    async function getData(){
       let headers= {
            Authorization: `Bearer ${token}`,
          }
        let {data}= await axios.get('https://alrayademo-back.appssquare.com/api/admin/job-titles',{headers});
        console.log(data);
        setuserData(data.data);
        console.log(userData)
    }
    async function setData(){
        const newJobTitle = {
            name: depInput.current.value,
            name_en: nameInput.current.value,
            name_ar: nameInput.current.value,
            department_id: 2,
            department_name: "medical",
          };
        let  headers= {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          }
          const body=JSON.stringify(newJobTitle);
        let data= await axios.post('https://alrayademo-back.appssquare.com/api/admin/job-titles',
        body,{headers}
        ).catch((error) => {
            console.log(error);
          })
          console.log(data)
          getData();
          nameInput.current.value=" ";
          depInput.current.value=" ";
    }
    async function deleteData(id){
      let  headers= {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      }
      let data= await axios.delete(`https://alrayademo-back.appssquare.com/api/admin/job-titles/${id}`,{headers}).
      catch((error) => {
        console.error(error);
      })
      getData();
      console.log(userData);
    }
    function updateData(id,i){
      document.getElementById("add").style.display="none";
      document.getElementById("update").style.display="block";
      nameInput.current.value= userData[i].name;
      depInput.current.value=userData[i].department_name;
      unique=id;
      index=i;
    }
    async function edit(){
      const updatedJops={
        name: depInput.current.value,
        name_en:nameInput.current.value,
        name_ar: nameInput.current.value,
        department_id: 2,
        department_name: "IT",
      }
      let  headers= {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      }
      const body=JSON.stringify(updatedJops);
      let {data}= await axios.put(`https://alrayademo-back.appssquare.com/api/admin/job-titles/${unique}`,body,{headers}).catch((error) => {
        console.error(error);
      });
      console.log(data.data);
      userData[index].name=nameInput.current.value;
      userData[index].department_name=depInput.current.value;
      getData();
      nameInput.current.value=" ";
      depInput.current.value=" ";
      document.getElementById("add").style.display="block";
      document.getElementById("update").style.display="none";
    }
    
  return (
    <>
    {userData[0]?<div className='container'>
        <div className='my-5 col-7 m-auto'>
            <input type="text" ref={nameInput} className='form-control ' placeholder=' Name' />
            <input type="text" ref={depInput}className='form-control my-2' placeholder=' Department'/>
            <button className='btn text-dark' onClick={setData} id='add'>Add +</button>
            <button className='btn text-dark ' id='update' onClick={()=>{edit()}}>Update</button>
        </div>
        <table className='table table-bordered table-hover text-center my-5'>
            <thead>
                <th>Name</th>
                <th>Department Name</th>
                <th>Update</th>
                <th>Delete</th>
            </thead>
            <tbody>
            {userData.map((el,i)=>{
                return<>
                <tr key={el.id} >
                    <td>{el.name}</td>
                    <td>{el.department_name}</td>
                    <td><button className='btn btn-info' onClick={()=>{updateData(el.id,i)}}><i className="fa-solid fa-pen text-dark"></i></button></td>
                    <td><button className='btn ' onClick={()=>{deleteData(el.id)}}><i className="fa-solid fa-trash-can text-danger"></i></button></td>
                </tr>
            </>
            })}
                
            </tbody>
        </table>
      
    </div>:<div className='z-3 d-flex vh-100  justify-content-center align-items-center'>
    
    <i class="fa-solid fa-spinner fa-spin  text-dark fa-4x"></i>
        </div>}
    </>
    
  )
}
