import React, { useContext } from 'react'
import { Link, Navigate, NavLink, useNavigate } from "react-router-dom";
import { CartContext } from '../context/CartContext';
import "./nav.css"
export default function Navbar() {
  let {userToken,setuserToken}=useContext(CartContext);
  let Navigate=useNavigate()
  console.log(userToken)

  function logOut(){
    setuserToken(false);
    localStorage.removeItem("token");
    Navigate("/login")
  }
  return (<>
    <nav className="navbar navbar-expand-lg shadow py-0">
    <div className="container">
      <Link className="navbar-brand text-info fs-2" to="home">
      Alraya
      </Link>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          
          {userToken?<>
          <li className="nav-item">
            <NavLink className={({isActive})=> isActive? "nav-link active": "nav-link"} aria-current="page" to="home">Home</NavLink>
          </li>
         
          </>:""}
          
          
          
        </ul>
        <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
          
          {userToken?<li className="nav-item">
            <span className="nav-link " style={{cursor:"pointer"}} onClick={logOut} >Logout</span>
          </li>:<li className="nav-item">
            <NavLink className="nav-link" to="login">Login</NavLink>
          </li>}
          
          
          
          
        </ul>
        
      </div>
    </div>
  </nav>
  </>
  )
}
